/* Tailwind - add this part */
@import '../css/screen.css';
@import './cards/audio.css';
@import './cards/before-after.css';
@import './cards/blockquote.css';
@import './cards/bookmark.css';
@import './cards/button.css';
@import './cards/callout.css';
@import './cards/file.css';
@import './cards/gallery.css';
@import './cards/header.css';
@import './cards/nft.css';
@import './cards/product.css';
@import './cards/toggle.css';
@import './cards/video.css';
@tailwind base;
@tailwind components;
@tailwind utilities;
.kg-toggle-card {
  @apply bg-gray-50;
}

.kg-callout-card-emoji,
.kg-callout-card-text .kg-callout-card-background-grey,
.kg-callout-card-background-white,
.kg-callout-card-background-blue,
.kg-callout-card-background-green,
.kg-callout-card-background-yellow,
.kg-callout-card-background-red,
.kg-callout-card-background-pink,
.kg-callout-card-background-purple,
.kg-callout-card-background-accent,
.kg-nft-logo,
.kg-video-thumbnail,
.kg-video-thumbnail.placeholder,
.kg-video-title,
.kg-button-card.kg-align-center {
  padding: auto;
}

.kg-toggle-card {
  @apply dark:text-[#111];
}

.newsletter {
  background: #fff;
  bottom: 0;
  height: 100%;
  left: 0;
  overflow: auto;
  position: absolute;
  right: 0;
  top: 0;
  transform: var(--transform);
  transition: all 0.2s ease;
  width: 100%;
  z-index: 999;
}

.owl-stage-outer {
  @apply py-10;
}

.it-featured-post .it-card {
  @apply bg-transparent;
}

/* Reset
/* ---------------------------------------------------------- */
li:not([class]) + li:not([class]) {
  margin-top: 0.5em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  line-height: 1.15em;
  font-weight: 800;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

h1 {
  font-size: 4.2rem;
  letter-spacing: -0.5px;
}

@media (max-width: 500px) {
  h1 {
    font-size: 2.7rem;
  }
}
h2 {
  font-size: 4rem;
}

@media (max-width: 500px) {
  h2 {
    font-size: 2.2rem;
  }
}
h3 {
  font-size: 3.5rem;
}

@media (max-width: 500px) {
  h3 {
    font-size: 1.8rem;
  }
}
h4 {
  font-size: 2.6rem;
}

h5 {
  font-size: 2.4rem;
}

h6 {
  font-size: 2.2rem;
}

.subscribe-form-button {
  background: var(--color-primary) !important;
  border: 1px solid transparent;
  padding: 1rem;
  color: var(--color-white);
  cursor: pointer;
  border-radius: var(--radius);
  font-weight: var(--font-bold);
}

.user-cover {
  display: block;
  overflow: hidden;
  width: 100%;
  height: 300px;
  margin: 0 0 30px;
  background: #fafafa no-repeat 50%;
  background-size: cover;
}