/* Buttons
/* ---------------------------------------------------------- */

.it-button {
    display: inline-block;
    min-width: 75px;
    height: var(--height);
    padding: 0 2rem;
    border: 0;
    border-radius: var(--radius);
    cursor: pointer;
    font-family: var(--font-sans-serif);
    font-size: 1.4rem;
    font-weight: var(--font-normal);
    line-height: var(--height);
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    appearance: none;
    transition: 0.025s ease;
}

.it-button.it-button-fit {
    width: 100%;
}

.it-button.it-button-small {
    height: calc(var(--height) * 0.9);
    line-height: calc(var(--height) * 0.9);
    padding: 0 1.5rem;
    font-size: 1.2rem;
}

.it-button.it-button-large {
    height: calc(var(--height) * 1.2);
    line-height: calc(var(--height) * 1.2);
    padding: 0 2.5rem;
    font-size: 1.7rem;
}

.it-button.it-button-disabled,
.it-button:disabled {
    pointer-events: none;
    opacity: 0.4;
}

.it-button {
    color: var(--color-primary) !important;
    background-color: transparent;
    box-shadow: inset 0 0 0 2px var(--color-primary);
}

.it-button:hover {
    text-decoration: none;
}

.it-button.it-button-primary {
    color: #fff !important;
    background-color: var(--color-base);
    box-shadow: none;
}

.it-button.it-button-primary:hover {
background-color: var(--color-primary);
}

.it-button.it-button-secondary {
    color: var(--color-base) !important;
    background-color: none;
    border: 0.1em solid var(--color-base);
    box-shadow: none;
}

.it-button.it-button-secondary:hover {
    color: var(--color-border) !important;
    background-color: var(--color-base);
    box-shadow: none;
}

.it-button.it-button-danger {
    color: #fff !important;
    background-color: var(--color-danger);
    box-shadow: none;
}

.it-nav-button {
    height: 3.2rem;
    line-height: 3.2rem;
    margin-right: 20px;
}

.it-head-menu .nav a {
    border: 0;
    outline: 0;
    box-shadow: none;
    font-size: inherit;
    padding-left: 0;
    color: var(--color-base) !important;
    text-decoration: none;
}

.signin .it-nav-button:hover {
background-color: var(--color-base);
color: var(--color-white);
}
.it-head-menu .nav a:hover {
    box-shadow: 0 -2px 0 0 var(--color-base) inset;
}